/*----------------------------------------------------------------------------
  WordPress Social Menu
  - make sure to include FontAwesome for this to work!
----------------------------------------------------------------------------*/
.menu-social {
  margin-left: -0.625rem;
  ul {
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    text-align: inherit;
    list-style: none;
    li{
      display: inline-block;
      position: relative;
      padding: 0 .625rem;
      a{
        display: inline-block;
        text-align: center;
        &:before{
          display: inline-block;
          font-style: normal;
          font-variant: normal;
          text-rendering: auto;
          -webkit-font-smoothing: antialiased;
          font-family: 'Font Awesome 5 Brands';
          font-weight: 400;
          font-size: rem(28);
          color: $white;
          transition: all .4s ease;
        }
        &:focus:before,
        &:hover:before {
          color: $teal;
        }
      } // end anchor
      // Adding the icons
      a[href*="facebook.com"]::before {
        content: '\f09a';
      }

      a[href*="twitter.com"]::before {
        content: '\f099';
      }

      a[href*="dribbble.com"]::before {
        content: '\f17d';
      }

      a[href*="plus.google.com"]::before {
        content: '\f0d5';
      }

      a[href*="pinterest.com"]::before {
        content: '\f0d2';
      }

      a[href*="github.com"]::before {
        content: '\f09b';
      }

      a[href*="tumblr.com"]::before {
        content: '\f173';
      }

      a[href*="youtube.com"]::before {
        content: '\f167';
      }

      a[href*="flickr.com"]::before {
        content: '\f16e';
      }

      a[href*="vimeo.com"]::before {
        content: '\f194';
      }

      a[href*="instagram.com"]::before {
        content: '\f16d';
      }

      a[href*="linkedin.com"]::before {
        content: '\f0e1';
      }

      a[href*="yelp.com"]::before {
        content: '\f1e9';
      }

      a[href*="mailto:"]::before {
        content: '\f003';
      }
    } // end li
  } // end ul
} // end .menu-social

// Hide the default Screen Reader text
.screen-reader-text {
  display: none;
}

// Media Queries 
@media screen and (max-width: $small-breakpoint) {
  .menu-social {
    margin-left: 0;
    margin-bottom: rem(30);
    
    ul {
      justify-content: center;
    }
  }
}